import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/chat/Index'
import userList from '@/components/userList/userList'
Vue.use(Router)

export default new Router({
routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/Index',
      name: 'Index',
      component: Index
    },
    {
      path: '/userList',
      name: 'userList',
      component: userList
    }
]
})



