<template>
  <div class="heading">
  		<span>{{msg}}</span>
  		<div class="btn-add">
  			<img src="../../../static/image/add.png">
  		</div>
  </div>
</template>

<script>

export default {
  name: 'heading',
  data () {
    return {
      msg: '聊天'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
	margin: 0;
	padding: 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.heading{
	width: 375px;
	height: 44px;
	line-height: 44px;
	text-align: center;
	font-size: 18px;
	background: #f5f5f5;
	border-bottom: 1px solid #d9d9d9;
	position: relative;
}
.btn-add{
	width: 17px;
	height: 18px;
	background:url('./static/image/add.png');
	background-size: 100% 100%;
	position: absolute;
	right: 15px;
	top: 14px;
}
.btn-add img{
	width: 100%;
	height: 100%;
	display: inherit;
}
</style>



