<template>
	<div class="footer">
		<ul class="footer-list">
			<router-link tag="li" :to="tab.router" v-for="(tab,i) in styleSwitch" :key='i'>
				<p v-on:click='active(tab,i)'>
					<i class="tx" >
						<img :src="defaultSrc+tab.url" >
					</i>
					<span>{{tab.title}}</span>
				</p>
			</router-link>
			<!--<router-link tag="li" to="/userList">
				<i>
					<img src="../../../static/image/tianshi.png">
				</i>
				<span>好友</span>
			</router-link>
			<li>
				<i class="tx1"><img src="../../../static/image/gongyi.png"></i>
				<span>热门</span></li>
			<li>
				<i class="tx2"><img src="../../../static/image/my.png"></i>
				<span>我的</span>
			</li>-->
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'foot',
		data() {
			return {
				defaultSrc: '../../../static/image/',
				styleSwitch: [{
						'id': 1,
						'url': 'liaotian.png',
						'router': '/index',
						'title': '聊天',
						'url1': 'liaotian.png',
						'url2': 'liaotian1.png'
					},
					{
						'id': 2,
						'url': 'tianshi.png',
						'router': '/userList',
						'title': '好友',
						'url1': 'tianshi.png',
						'url2': 'tianshi1.png'
					},
					{
						'id': 3,
						'url': 'gongyi.png',
						'router': '/NUll',
						'title': '热门',
						'url1': 'gongyi.png',
						'url2': 'gongyi1.png'
					},
					{
						'id': 4,
						'url': 'my.png',
						'router': '/NULl',
						'title': '我的',
						'url1': 'my.png',
						'url2': 'my1.png'
					}
				]
			}
		},
		methods: {
			active(tab, i) {
				//footer导航栏图片切换
				for(let j =0;j<this.styleSwitch.length;j++){
					this.styleSwitch[j].url =this.styleSwitch[j].url1
				}
				tab.url=tab.url2
			}
		},
		computed:{
			
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	li {
		list-style: none;
	}
	
	ul {
		list-style-type: none;
		padding: 0;
		background: #f0f0f0;
	}
	
	.footer-list {
		width: 100%;
		height: 80px;
		display: flex;
		display: -ms-flexbox;
		display: -webkit-flex;
		align-items: center;
		position: fixed;
		bottom: 0;
	}
	
	.footer-list li {
		flex: 1;
		text-align: center;
	}
	
	.footer-list i {
		width: 21px;
		height: 18px;
		display: block;
		text-align: center;
		margin: 0 auto;
		margin-bottom: 5px;
		/*no*/
	}
	
	.footer-list i:first-child {
		width: 36.5px;
		height: 19px;
	}
	
	.footer-list i.tx {
		width: 22.5px;
		height: 21px;
	}
	
	.footer-list li:nth-of-type(2) i{
		width: 30px;
		height: 18px;
	}
	
	.footer-list li:nth-of-type(3) i{
		width: 18px;
		height: 19px;
	}
	
	.footer-list i img {
		width: 100%;
		height: 100%;
	}
	
	.footer-list li span {
		font-size: 12px;
		color: #7f7e7e;
	}
	
	.router-link-active span {
		color: #f26715 !important;
	}
</style>


